html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.compensate-for-scrollbar {
  padding-right: 0 !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  cursor: pointer;
  background: none;
  font-weight: inherit;
  font-size: inherit;
}

select {
  background-color: inherit;
}

blockquote {
  margin: 0;
}

button {
  border: none;
  font-family: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit;
}

textarea {
  outline: none;
  border: none;
  resize: none;
}

textarea:focus {
  outline-width: 0;
}

input,
input::placeholder {
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  border: none;
  outline: none;
}

input::placeholder {
  color: inherit;
}

picture {
  display: block;
}
picture img {
  width: 100%;
  max-width: 100%;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  margin-block-end: 0;
  margin-block-start: 0;
}

ul li {
  list-style-type: none;
}

ol {
  margin: 0;
  padding: 0;
}

ol li {
  list-style-position: inside;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

*, *::after, *::before {
  padding: 0;
  margin: 0;
}

.to_animate {
  transition: opacity 0.8s, transform 0.8s;
}

.draw-center {
  transition: mask-size 2s;
}
.draw-center.to_animate {
  mask-image: url("../img/animations/mask.svg");
  mask-repeat: repeat-y;
  mask-position: center;
  mask-size: 0;
}
.draw-center.to_animate.animated {
  mask-size: 100%;
}

.draw-left {
  transition: mask-size 2s;
}
.draw-left.to_animate {
  mask-image: url("../img/animations/mask.svg");
  mask-repeat: repeat-y;
  mask-size: 0;
}
.draw-left.to_animate.animated {
  mask-size: 100%;
}

.down-fade.to_animate {
  opacity: 0;
  transform: translateY(50px);
}
.down-fade.to_animate.animated {
  opacity: 1;
  transform: translateY(0);
}

.up-fade.to_animate {
  opacity: 0;
  transform: translateY(-50px);
}
.up-fade.to_animate.animated {
  opacity: 1;
  transform: translateY(0);
}

.left-fade.to_animate {
  opacity: 0;
  transform: translateX(-50px);
}
.left-fade.to_animate.animated {
  opacity: 1;
  transform: translateX(0);
}

.right-fade.to_animate {
  opacity: 0;
  transform: translateX(50px);
}
.right-fade.to_animate.animated {
  opacity: 1;
  transform: translateX(0);
}

.rotate-left.to_animate {
  transform: rotate3d(0, 1, 0, 90deg);
}
.rotate-left.to_animate.animated {
  transform: none;
}

.rotate-up.to_animate {
  transform: rotate3d(0, 0, 1, 90deg);
}
.rotate-up.to_animate.animated {
  transform: none;
}

.fade-out.to_animate {
  opacity: 0;
  transform: scale(0.5);
}
.fade-out.to_animate.animated {
  opacity: 1;
  transform: none;
}

.fade-in.to_animate {
  opacity: 0;
  transform: scale(1.5);
}
.fade-in.to_animate.animated {
  opacity: 1;
  transform: none;
}

html {
  scrollbar-gutter: stable;
}

body {
  font-family: "Inter";
}
body.lock {
  overflow: hidden;
}

.page {
  overflow: hidden;
  padding-top: 82px;
}
@media (min-width: 1024px) {
  .page {
    padding-top: 109px;
  }
}

.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.hamRotate.active {
  transform: rotate(45deg);
}

.hamRotate180.active {
  transform: rotate(180deg);
}

.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #2B3746;
  stroke-width: 5.5;
  stroke-linecap: round;
}

.ham1 .top {
  stroke-dasharray: 40 139;
}

.ham1 .bottom {
  stroke-dasharray: 40 180;
}

.ham1.active .top {
  stroke-dashoffset: -98px;
}

.ham1.active .bottom {
  stroke-dashoffset: -138px;
}

.primary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 48px;
  background-color: #EBF3FE;
  gap: 15px;
  border-radius: 20px;
  transition: 0.3s;
}
.primary-button__plus {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background-color: #FFFFFF;
  position: relative;
  transition: background-color 0.3s;
}
.primary-button__plus::after {
  content: "";
  display: block;
  width: 12px;
  height: 2px;
  position: absolute;
  top: 15px;
  left: 10px;
  background-color: #82ADFD;
}
.primary-button__plus::before {
  content: "";
  display: block;
  width: 2px;
  height: 12px;
  position: absolute;
  top: 10px;
  left: 15px;
  background-color: #82ADFD;
  transition: transform 0.3s;
}
.primary-button__text {
  font-weight: 600;
  font-size: 16px;
  color: #6398FD;
}
@media (hover: hover) {
  .primary-button:hover {
    background-color: rgba(130, 173, 253, 0.3019607843);
  }
  .primary-button:hover .primary-button__plus::before {
    transform: rotate(90deg);
  }
}
@media (max-width: 1023px) {
  .primary-button {
    padding: 14px;
    border-radius: 10px;
  }
  .primary-button__text {
    font-size: 16px;
  }
}

.nav-buttons {
  display: flex;
  gap: 10px;
}

.nav-button {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: 0.3s;
}
.nav-button svg {
  max-height: 30px;
  filter: brightness(1);
  transition: 0.3s;
}
.nav-button_blue {
  background-color: rgba(208, 224, 254, 0.4);
}
.nav-button_blue.swiper-button-disabled {
  opacity: 0.5;
}
.nav-button_blue path {
  fill: #4A7AD4;
}
@media (hover: hover) {
  .nav-button_blue:not(.swiper-button-disabled):hover {
    background-color: #4A7AD4;
  }
  .nav-button_blue:not(.swiper-button-disabled):hover svg {
    filter: brightness(3);
  }
}
.nav-button_white {
  background-color: #FFFFFF;
}
.nav-button_white path {
  fill: #82ADFD;
}
.nav-button_back {
  transform: rotate(180deg);
}

