.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 2000;
  transition: opacity 0.4s ease;
  background: rgba(0, 8, 84, 0.27);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  display: none;
}

.popup.active {
  display: block;
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 15px 20px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  border-radius: 20px;
  background-color: #FFFFFF;
  border: 1px solid #F1F2F9;
  position: relative;
  scale: 0.3;
  transition: scale 0.4s ease;
  box-shadow: 0px 0px 30px 0px rgba(255, 255, 255, 0.1);
}

.popup.open .popup__content {
  scale: 1;
}

.close-popup {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background-color: #FFFFFF;
  border: 1px solid #F1F2F9;
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.close-popup svg {
  width: 16px;
  fill: #3C3C43;
  transition: 0.3s;
}

@media (hover: hover) {
  .close-popup:hover {
    background-color: rgba(60, 60, 67, 0.4392156863);
