.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 2000;
  transition: opacity 0.4s ease;
  background: rgba(0, 8, 84, 0.27);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  display: none;
}

.popup.active {
  display: block;
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 15px 20px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  border-radius: 20px;
  background-color: $bg-prim;
  border: 1px solid #F1F2F9;
  position: relative;
  scale: 0.3;
  transition: scale 0.4s ease;
  box-shadow: 0px 0px 30px 0px rgba(255, 255, 255, 0.1);
}

.popup.open .popup__content {
  scale: 1;
}

.close-popup {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background-color: $bg-prim;
  border: 1px solid #F1F2F9;
  cursor: pointer;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;

  svg {
    width: 16px;
    fill: #3C3C43;
    transition: 0.3s;
  }
}

@media (hover: hover) {
  .close-popup:hover {
    background-color: #3c3c4370;

    svg {
      fill: #FFF;
    }
  }
}

.map-popup {
  .popup__content {
    height: 80vh;
    width: 90vw;
    position: relative;
    padding: 10px;
  }
}

#map {
  width: 100%;
  height: 100%;
}

.select-city {
  .popup__body {
    padding: 0;
  }

  .popup__content {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    padding: 30px 20px;
  }

  .close-popup {
    right: 20px;
    top: 20px;
  }

  .popup-title {
    font-size: 24px;
    line-height: 32px;
    color: $prim;
    margin-bottom: 30px;
    font-weight: 600;
  }

  &__input-wrapper {
    position: relative;
    margin-bottom: 40px;

    svg {
      position: absolute;
      width: 26px;
      height: 26px;
      top: 11px;
      right: 16px;
    }
  }

  &__input {
    padding: 12px 16px;
    padding-right: 48px;
    width: 100%;
    border: 1px solid #2B37461A;
    border-radius: 10px;
    color: $prim;
    font-size: 16px;
    line-height: 24px;
  }

  &__results-title {
    font-size: 18px;
    line-height: 26px;
    color: $prim;
    font-weight: 700;
    margin-bottom: 20px;
  }

  &__results {
    padding-bottom: 20px;
    max-height: calc(100vh - 230px);
    overflow-y: auto;

    li {
      a {
        display: flex;
        gap: 10px;
        align-items: center;

        span {
          padding: 2px 8px;
          font-size: 12px;
          line-height: 16px;
          font-weight: 600;
          color: $blue-color;
          background-color: $bg-ter;
        }

        p {
          color: $prim;
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          transition: color 0.5s;
        }

        @media(hover : hover) {
          &:hover {
            p {
              color: $blue-color
            }
          }
        }
      }

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    @media(min-width: 660px) {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;

      li {
        flex-basis: calc(50% - 10px);

        :not(:last-child) {
          margin-bottom: 0;
        }
      }
    }
  }
}

@media(min-width: 1024px) {
  .select-city {
    &.open {
      .popup__content {
        left: 0;
      }
    }

    .popup__content {
      scale: 1;
      max-width: 750px;
      margin-left: auto;
      border-radius: 40px 0 0 40px;
      padding: 60px;
      padding-right: 30px;
      position: relative;
      left: 750px;
      transition: 0.5s;

      .close-popup {
        top: 60px;
        right: 30px;
      }
    }

    .popup-title {
      font-size: 36px;
      line-height: 42px;
      margin-bottom: 40px;
    }

    &__input-wrapper {
      margin-bottom: 60px;
    }

    &__results-title {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 30px;
    }

    &__input {
      font-size: 18px;
      line-height: 26px;
    }

    &__results {
      column-gap: 40px;
      row-gap: 14px;

      li {
        flex-basis: calc(50% - 20px);

        a {
          gap: 10px;

          span {
            padding: 2px 10px;
            border-radius: 6px;
            font-size: 14px;
            line-height: 20px;
          }

          p {
            font-size: 18px;
            line-height: 26px;
          }
        }
      }
    }
  }
}

.review-popup {
  .popup-title {
    font-size: 20px;
    line-height: 28px;
    color: $prim;
    margin-bottom: 20px;
    font-weight: 600;
  }

  .popup__form-wrapper {
    max-height: calc(100vh - 178px);
    overflow: auto;
    padding-bottom: 30px;
  }

  .popup__body {
    padding: 0;
  }

  .popup__content {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    padding: 30px 20px;
  }

  .close-popup {
    right: 20px;
    top: 20px;
  }

  .popup-form {
    padding: 14px;
    border-radius: 20px;
    border: 1px solid #EDEDEE;

    &__text {
      color: #2B374680;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 10px;
    }

    &__item {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 14px;
      padding-bottom: 14px;
      border-bottom: 1px solid #EDEDEE;

      &:last-child{
        
      }

      label {
        font-size: 14px;
        line-height: 20px;
        color: $prim;
        margin-bottom: 8px;
        font-weight: 600;
      }

      input {
        font-size: 16px;
        line-height: 24px;
        color: $prim;
        padding: 12px 16px;
        border: 1px solid #2B37461A;
        border-radius: 10px;
        width: 100%;

        &::placeholder {
          color: #2B374699;
        }
      }

      textarea {
        width: 100%;
        padding: 12px 16px;
        border: 1px solid #2B37461A;
        border-radius: 10px;
        color: $prim;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 30px;

        &::placeholder {
          color: #2B374699;
        }
      }
    }

    &__politic {
      display: flex;
      gap: 8px;
      align-items: flex-start;

      input {
        width: 16px;
      }

      p {
        font-size: 12px;
        line-height: 16px;
        color: #2B374699;
      }
    }

    &__button {
      color: $bg-prim;
      font-size: 16px;
      line-height: 24px;
      padding: 16px;
      background-color: $blue-color;
      width: 100%;
    }
  }
}

@media(min-width: 1024px) {
  .review-popup {
    &.open {
      .popup__content {
        left: 0;
      }
    }

    .popup-title {
      font-size: 36px;
      line-height: 42px;
      margin-bottom: 40px;
    }

    .popup-form {
      &__item {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
      }
    }

    .popup__content {
      scale: 1;
      max-width: 750px;
      margin-left: auto;
      border-radius: 40px 0 0 40px;
      padding: 60px;
      padding-right: 30px;
      position: relative;
      left: 750px;
      transition: 0.5s;

      .close-popup {
        top: 60px;
        right: 30px;
      }
    }
  }
}

.rating-area {
  overflow: hidden;
}

.rating-area:not(:checked)>input {
  display: none;
}

.rating-area:not(:checked)>label {
  float: right;
  width: 42px;
  padding: 0;
  cursor: pointer;
  font-size: 32px;
  line-height: 32px;
  color: lightgrey;
  text-shadow: 1px 1px #bbb;
}

.rating-area:not(:checked)>label:before {
  content: '';
}

.rating-area>input:checked~label {
  color: gold;
  text-shadow: 1px 1px #c60;
}

.rating-area:not(:checked)>label:hover,
.rating-area:not(:checked)>label:hover~label {
  color: gold;
}

.rating-area>input:checked+label:hover,
.rating-area>input:checked+label:hover~label,
.rating-area>input:checked~label:hover,
.rating-area>input:checked~label:hover~label,
.rating-area>label:hover~input:checked~label {
  color: gold;
  text-shadow: 1px 1px goldenrod;
}

.rate-area>label:active {
  position: relative;
}